import { AbstractControl, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { AddVehicleForm } from 'src/app/model/chq-new-case-form.model';
import { CustomValidationResult } from 'src/app/model/chq-validation-model';
import { ChqWidgetsHeaderModel } from 'src/app/model/chq-widgets-header-model';

export const newVehicleForm = (): AddVehicleForm => {
  return {
    vin: {
      placeHolder: 'search_vin',
      name: 'vin',
      label: 'vin_number',
      type: 'text',
      value: 0,
      icon: 'search',
      maxLength: 17,
      validation: {
        name: 'vin',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.minLength(17), Validators.pattern('^[a-hA-Hj-nJ-NpPr-zR-Z0-9]+$') ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['pattern']) {
            return 'pattern_vin'
          }
          if (error['minlength']) {
            return 'min_length_vin'
          }
          return '';
        }
      }
    },
    make: {
      placeHolder: 'make',
      name: 'make',
      label: 'make',
      options: [ {} ],
      displayValue: 'name',
      fieldValue: 'name',
      type: 'select',
      value: 0,
      validation: {
        name: 'make',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.required ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'required_make'
          }
          return '';
        }
      },
    },
    model: {
      placeHolder: 'model',
      name: 'model',
      label: 'model',
      options: [ {} ],
      displayValue: 'modelDescription',
      fieldValue: 'modelDescription',
      type: 'select',
      value: 0,
      validation: {
        name: 'model',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.required ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'required_model'
          }
          return '';
        }
      }
    },
    year: {
      placeHolder: 'import_interval',
      label: 'import_interval',
      name: 'year',
      //selectedOption: '',
      options: [ {} ],
      displayValue: 'name',
      fieldValue: 'name',
      type: 'select',
      value: 0,
      validation: {
        name: 'year',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.required ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'required_year'
          }
          return '';
        }
      }
    },
    licensePlate: {
      placeHolder: 'license_plate',
      label: 'license_plate',
      name: 'licensePlate',
      value: 0,
      type: 'text',
      maxLength: 12,
      validation: {
        name: 'licensePlate',
        validationFunction: (): ValidatorFn => { 
          return Validators.compose([ Validators.required, Validators.maxLength(12), Validators.pattern('^[a-zA-Z0-9]*$'), licensePlateValidator() ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'required_license_plate'
          }
          if (error['maxlength']) {
            return 'max_length_license_plate'
          }
          if (error['pattern']) {
            return 'pattern_digit_alphabets'
          }
          if (error['invalidLicensePlate']) {
            return 'invalid_license_plate'
          }
          return '';
        }
      }
    },
    carMilege: {
      placeHolder: 'car_mileage',
      label: 'mileage',
      name: 'carMilege',
      value: 0,
      type: 'text',
      maxLength: 7,
      validation: {
        name: 'carMilege',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.required, Validators.maxLength(7), Validators.pattern('^[0-9]+$'), carMileageValidator() ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'required_car_mileage'
          }
          if (error['maxlength']) {
            return 'max_length_car_mileage'
          }
          if (error['pattern']) {
            return 'pattern_numeric'
          }
          if(error['invalidCarMileage']) {
            return 'invalid_car_mileage'
          }
          return '';
        }
      }
    },
    registrationDate: {
      placeHolder: 'first_reg_mfg_date',
      label: 'reg_mfg_date',
      name: 'registrationDate',
      value: '',
      type: 'date',
      maxDate: new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate() - 1),
      minDate: new Date('1980-01-01'),
      disabled: true,
      validation: {
        name: 'registrationDate',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.required ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'required_date'
          }
          return '';
        }
      }
    }
  }
};

export const newVehicleHeader: ChqWidgetsHeaderModel = {
  title: 'add_vehicle',
  step: 2,
  description: 'add_vehicle_msg',
  stepName: 'step_two',
};

/**
 * 
 * @returns 
 */
export function carMileageValidator() : ValidatorFn {
  const result: CustomValidationResult = { 'invalidCarMileage': false };
  return (control: AbstractControl): ValidationErrors | null => {
    if (control.value < 1 && control.value != '') {
      result['invalidCarMileage'] = true;
      return result;
    }
    return null;
  }
}

/**
 * License plate Validator
 * @returns 
 */
export function licensePlateValidator() : ValidatorFn {
  const result: CustomValidationResult = { 'invalidLicensePlate': false };
  return (control: AbstractControl): ValidationErrors | null => {
    if (control.value === '0') {
      result['invalidLicensePlate'] = true;
      return result;
    }
    return null;
  }
}